import DebugUtility from '../function/DebugUtility';

const DeviceUtility = {

    /*  start */
    eventBackButton() {
        // Toast
        DebugUtility.toast('!');
        document.removeEventListener("backbutton", DeviceUtility.eventBackButton, false); // 
        document.addEventListener("backbutton", DeviceUtility.exitApp, false);//
        // 3
        var intervalID = window.setInterval(function() {
            window.clearInterval(intervalID);
            document.removeEventListener("backbutton", DeviceUtility.exitApp, false); // 
            document.addEventListener("backbutton", DeviceUtility.eventBackButton, false); // 
        }, 3000);
    },
    exitApp() {
        navigator.app.exitApp();
    }
    /*  end */

};

export default DeviceUtility;



